
class Authenticate_user{
    constructor(){
        this.authenticated = false;
        this.user = null;
        this.token = null;
    }

    login(callback){
        this.authenticated=true;
        callback();
    }

    logout(callback){
        this.authenticated=false
        callback()
    }
    isAuth(){
        return this.authenticated
    }
    getUser(){
        return this.user
    }
    setToken(tk){
        this.token = tk
        return
    }
    setUser(user){
        this.user = user
        return
    }
}

export default new Authenticate_user();